* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  height: 100%;
}

body {
  font-family: Helvetica, Arial, sans-serif;
  color: #333e5a;
  min-height: 100%;
}

header {
  background: #f0293e;
  color: #fff;
  text-align: center;
  flex-grow: 0;
  margin-bottom: 2em;
}

h1 {
  font-weight: 300;
  padding: 0.4em 0;
}

#root {
  min-height: 100vh;
}

.app {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

main {
  background: #ffffff;
  flex-grow: 1;
}

form {
  max-width: 300px;
  margin: 0 auto;
}

h2 {
  font-weight: 300;
  margin-bottom: 1em;
  text-align: center;
}

form > div {
  width: 100%;
  margin-bottom: 1em;
}
form > div > label {
  display: block;
  margin-bottom: 0.3em;
}
form > div > input {
  display: block;
  width: 100%;
  font-size: 16px;
  padding: 0.4em;
  border-radius: 6px;
  border: 1px solid #333e5a;
}

button {
  background: #333e5a;
  color: #fff;
  font-size: 16px;
  padding: 0.4em;
  border-radius: 6px;
  border: 1px solid black;
}
button:hover {
  filter: brightness(150%);
}

.room {
  position: relative;
}
.room button {
  display: inline-block;
  align-items: right;
  float: right;
  right: 20px;
}
.room > h3 {
  text-align: center;
  font-weight: 300;
  margin-bottom: 1em;
}


footer {
  background: #333e5a;
  color: #fff;
  text-align: center;
  flex-grow: 0;
  padding: 1em 0;
}

footer a {
  color: #fff;
}

.closeMeetingButton{
  background-color: #f0293e;
}

.popupMessage{
  position:absolute; 
  top:30vh;
  left:40vw;
  

  border: black;
  border-style: solid;
  border-radius: 5px;

  stroke: grey;
  stroke-width: 1px;

  background-color: #333e5a;

  padding: 30px;

  color: #f0f0f0;
  text-align: center;

}
.popupMessage button{
  margin: 20px;
}

@media screen and (max-width: 530px) {
  /* comes into effect for screens less than or equal to 320 pixels */
  .closeMeetingButton{
    margin-top: 10px;
  }
  .roomButtons{
    width: min(40vw,150px);
    max-height: 20vh;
    min-height: max(120px,20vh);
    position: fixed;
    bottom: 0vh;
    right: 0vh;
    text-align: right;

  }
  .roomButtons button{
      display: inline-block;
      margin-bottom: 5px;
      font-size: min(min(5vw,16px), 5vh);
      
  }
  .roomButtons p{
    display: inline-block;
    font-size: min(5vw,16px);
}

  .local-participant {
    text-align: center;
    /* height: 10vh; */
    width: 20vh;
    min-width: 100px;
    min-height: 100px;
    position: fixed;
     bottom: 0vh;
     left: 0vh;
     
  }
  

  .remote-participants {
    display: inline-block;
    flex-wrap: nowrap;
    justify-content: space-between;
    /* padding: 0 2em 2em; */
  }
  .participant {
    background: #333e5a;
    
    padding: 5px;
    border-radius: 3px;
    display: inline-block;
    margin-right: 10px;
  }
  .participant:last-child {
    margin-right: 0;
  }
  .participant h3 {
    text-align: center;
    padding-bottom: 0.5em;
    color: #fff;
  }
  
  video {
    width: 100%;
    max-width: 600px;
    max-height: 40vh;
    display: block;
    margin: 0 auto;
    border-radius: 6px;
  }
 
}
@media screen and (min-width: 531px) {
  /* comes into effect for screens between 321 and 480 pixels (inclusive) */
  .roomButtons{
    float: right;    
  }
  .roomButtons button{
      display: inline-block;
  }
  .local-participant {
    text-align: center;
    margin-bottom: 2em;
    width: 33%;
    display: inline-flex;
  }
  .remote-participants {
    display: inline-flex;
    width: 67%;
    max-height: 80vh;
    flex-wrap: nowrap;
    justify-content: space-between;
    padding: 0 2em 2em;
  }
  .participant {
    background: #333e5a;
    padding: 10px;
    border-radius: 6px;
    display: inline-block;
    margin-right: 10px;
  }
  .participant:last-child {
    margin-right: 0;
  }
  .participant h3 {
    text-align: center;
    padding-bottom: 0.5em;
    color: #fff;
  }
  
  video {
    width: 100%;
    max-width: 600px;
    display: block;
    margin: 0 auto;
    border-radius: 6px;

  }
  
}